.top {
  display: flex;
  flex-direction: row;
  height: 33.1875rem;
}
.top__container {
  margin: auto;
  overflow: hidden;
  width: 52.3125rem;
}
.top__first {
  display: flex;
  position: relative;
  width: 52.3125rem;
}
.top__first img {
  width: 52.3125rem;
  height: 33.1875rem;
}
.top__heading {
  font-weight: 700;
}
.top__list {
  list-style: none;
  display: flex;
  flex-direction: row;
}
.top__list li {
  padding-left: 1.5625rem;
  color: var(--white);
  z-index: 1;
}
.top__nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 3.8125rem;
  margin-top: 3.3125rem;
  cursor: pointer;
  position: absolute;
  top: 0;
  z-index: 1 !important;
}
.top__nav li {
  cursor: pointer;
}
.top__nav p {
  color: var(--white);
  font-weight: 600;
  font-size: 1.5625rem;
}
.top__second {
  background: var(--white);
  padding-top: 6.625rem;
  padding-left: 5.8125rem;
  padding-right: 6rem;
  padding-bottom: 9.0625rem;
  position: relative;
  width: 37.6875rem;
}
.top__shop {
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}
.top__shop p {
  letter-spacing: 0.5rem;
  margin-right: 2.25rem;
}
.top__slider {
  display: flex;
  flex-direction: row;
  width: 10rem;
  background-color: var(--black);
  height: 5rem;
  float: none;
  position: absolute;
  bottom: 0;
  left: 0;
}
.top__slider1 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 5rem;
  width: 5rem;
  background-repeat: no-repeat;
  cursor: pointer;
}
.top__slider1:hover {
  background-color: var(--very-dark-grey);
}
.top__slider2 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 5rem;
  width: 5rem;
  background-repeat: no-repeat;
  cursor: pointer;
}
.top__slider2:hover {
  background-color: var(--very-dark-grey);
}
.top__text {
  font-weight: 500;
  color: var(--dark-grey);
}
.top__text2 {
  font-weight: 600;
}

.buttom {
  display: flex;
  flex-direction: row;
  height: 16.5625rem;
}
.buttom__first {
  background-image: url(/images/image-about-dark.jpg);
  width: 26.1875rem;
}
.buttom__second {
  width: 36.1875rem;
  padding-top: 4rem;
  padding-left: 2.75rem;
  padding-right: 2.875rem;
  padding-bottom: 4.25rem;
}
.buttom__second p {
  color: var(--dark-grey);
}
.buttom__third {
  background-image: url(/images/image-about-light.jpg);
  width: 26.875rem;
  height: 100%;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-family: var(--font-spartan);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --font-spartan: "Spartan", sans-serif;
}

:root {
  --dark-grey: hsl(0, 0%, 63%);
  --black: hsl(0, 0%, 0%);
  --white: hsl(0, 0%, 100%);
  --very-dark-grey: hsl(0, 0%, 27%);
}